* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Helvetica Neue', Helvetica, sans-serif;
  line-height: 1.5;
  margin: 0;
  color: #111;
  background-color: #fff;
}

svg {
  max-height: 100%;
}

a {
  color: #07c;
}

h1,
h2,
h3 {
  font-weight: 600;
  line-height: 1.25;
  margin-top: 1em;
  margin-bottom: .5em;
}

h1 {
  font-size: 32px;
  font-size: 2rem;
}

h2 {
  font-size: 24px;
  font-size: 1.5rem;
}

h3 {
  font-size: 20px;
  font-size: 1.25rem;
}

p,
ul {
  margin-top: 1em;
  margin-bottom: 1em;
}

.h1 {
  font-size: 32px;
  font-size: 2rem;
}

.h3 {
  font-size: 20px;
  font-size: 1.25rem;
}

.h4 {
  font-size: 16px;
  font-size: 1rem;
}

.h5 {
  font-size: 14px;
  font-size: .875rem;
}

.h6 {
  font-size: 12px;
  font-size: .75rem;
}

.text-decoration-none {
  text-decoration: none;
}

.bold {
  font-weight: bold;
  font-weight: bold;
}

.regular {
  font-weight: normal;
}

.caps {
  text-transform: uppercase;
  letter-spacing: .2em;
}

.center {
  text-align: center;
}

.nowrap {
  white-space: nowrap;
}

.list-reset {
  list-style: none;
  padding-left: 0;
}

.inline-block {
  display: inline-block;
}

.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

.max-width-4 {
  max-width: 1024px;
  max-width: 64rem;
}

.ml1 {
  margin-left: 8px;
  margin-left: .5rem;
}

.mt2 {
  margin-top: 16px;
  margin-top: 1rem;
}

.mt3 {
  margin-top: 32px;
  margin-top: 2rem;
}

.mr3 {
  margin-right: 32px;
  margin-right: 2rem;
}

.my3 {
  margin-top: 32px;
  margin-top: 2rem;
  margin-bottom: 32px;
  margin-bottom: 2rem;
}

.my4 {
  margin-top: 64px;
  margin-top: 4rem;
  margin-bottom: 64px;
  margin-bottom: 4rem;
}

.mxn2 {
  margin-left: -16px;
  margin-left: -1rem;
  margin-right: -16px;
  margin-right: -1rem;
}

.mx-auto {
  margin-left: auto;
  margin-right: auto;
}

.px1 {
  padding-left: 8px;
  padding-left: .5rem;
  padding-right: 8px;
  padding-right: .5rem;
}

.p2 {
  padding: 16px;
  padding: 1rem;
}

.px2 {
  padding-left: 16px;
  padding-left: 1rem;
  padding-right: 16px;
  padding-right: 1rem;
}

.py3 {
  padding-top: 32px;
  padding-top: 2rem;
  padding-bottom: 32px;
  padding-bottom: 2rem;
}

.py4 {
  padding-top: 64px;
  padding-top: 4rem;
  padding-bottom: 64px;
  padding-bottom: 4rem;
}

.border {
  border-style: solid;
  border-width: 1px;
}

.border-top {
  border-top-style: solid;
  border-top-width: 1px;
}

.border-bottom {
  border-bottom-style: solid;
  border-bottom-width: 1px;
}

.h00 {
  font-size: 64px;
  font-size: 4rem;
}

.black {
  color: #111111;
}

.bg-cover {
  background-size: cover;
}

.bg-center {
  background-position: center;
}

body {
  font-family: 'Inconsolata', monospace;
}

header {
  background: url("img/bg-1024.jpg");
  filter: url('data:image/svg+xml;charset=utf-8, <svg xmlns="http://www.w3.org/2000/svg"><filter id="filter"><feGaussianBlur in="SourceAlpha" stdDeviation="5" /><feOffset dx="1" dy="1" result="offsetblur" /><feFlood flood-color="rgba(0, 0, 0, 1)" /><feComposite in2="offsetblur" operator="in" /><feMerge><feMergeNode /><feMergeNode in="SourceGraphic" /></feMerge></filter></svg>#filter');
  -webkit-filter: drop-shadow(0 0 5px);
  filter: drop-shadow(0 0 5px);
}

header section {
  filter: url('data:image/svg+xml;charset=utf-8, <svg xmlns="http://www.w3.org/2000/svg"><filter id="filter"><feGaussianBlur stdDeviation="0" /><feComponentTransfer color-interpolation-filters="sRGB"><feFuncA type="table" tableValues="0 1" /></feComponentTransfer></filter></svg>#filter');
  -webkit-filter: drop-shadow(-1px 1px 0.6px slategrey) blur(0px) opacity(100%);
  filter: drop-shadow(-1px 1px 0.6px slategrey) blur(0px) opacity(100%);
}

header h1 {
  font-family: "Roboto Mono", sans-serif;
  font-weight: 700;
}

article p {
  font-family: "Roboto Mono", sans-serif;
  font-weight: 400;
  font-style: italic;
}

.caps-r {
  text-transform: uppercase;
}

.logo {
  fill: currentColor;
}

@media (min-width: 40em) {
  .sm-col {
    float: left;
    box-sizing: border-box;
  }

  .sm-col-4 {
    width: 33.33333%;
  }

  .sm-col-6 {
    width: 50%;
  }

  .sm-col-8 {
    width: 66.66667%;
  }

  .sm-left-align {
    text-align: left;
  }
}

@media (min-width: 52em) {
  .md-col-3 {
    width: 25%;
  }

  .md-col-9 {
    width: 75%;
  }

  .h00-responsive {
    font-size: 8vw;
  }
}

@media (min-width: 96em) {
  .h00-responsive {
    font-size: 7.68rem;
  }
}

